import React from 'react';
import ReactDOM from 'react-dom'
import './index.css'

class Button extends React.Component {
  render() {
    return (
      <button className="click" onClick={this.props.onClick}>
        {"Make Big Buck: " + this.props.value}
      </button>
    );
  }
}
  
class Board extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      points: 0,
      buttons: [
        this.renderSquare(0, 1),
      ],
      incomes: [
        1,
      ],
      cost: 100,
    };
  }

  handleClick(i) {
    this.setState({
      points: this.state.points + this.state.incomes[i],
    })
    this.checkNewButton();
  }

  renderSquare(i, j) {
    return <Button value={j} onClick={() => this.handleClick(i)} />
  }

  checkNewButton() {
    if (this.state.points >= this.state.cost) {
      const newcome = 2**this.state.buttons.length;
      this.setState({
        cost:  Math.round(100**(0.8 * (this.state.buttons.length + 1))),
        buttons: this.state.buttons.concat([this.renderSquare(1, newcome)]),
        incomes: this.state.incomes.concat([newcome])
      });
    }
  }

  render() {
    const status = "Points: " + this.state.points + "\nNext Button: " + this.state.cost;
    const clicky = this.state.buttons.map((button, i) => {
      return (
        <li key={"buttonProfit" + this.state.incomes[i]}>{button}</li>
      )
    })

    return (
      <div id="Based">
        <div className="status">{status}</div>
        <ol>
          {clicky}
        </ol>
      </div>
    );
  }
}

class Game extends React.Component {
  render() {
    return (
      <div className="game">
        <div className="game-board">
          <Board />
        </div>
        <div className="game-info">
          <div>{/* status */}</div>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);
